import NewsChat from "./newsChat-model.js";
const newsChat = new NewsChat();
export default {
	data() {
		return {
			options: {},
			userInfo: {},
			message: ''
		};
	},
	onLoad(options) {
		const that = this
		that.options = options
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.getNewsList(() => {
				callBack && callBack();
			})
		},
		getNewsList(callBack) {
			const that = this
			newsChat.getNewsList({
				openid: that.userInfo.openid,
				id: that.options.id
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					
				}
				callBack && callBack();
			})
		},
		// 发送消息
		sendMessage(callBack) {
			const that = this
			newsChat.sendMessage({
				openid: that.userInfo.openid,
				id: that.options.id,
				content: this.message
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					
				} else {
					newsChat.show_tips(res.explain)
				}
				// callBack && callBack();
			})
		},
		getMessage(e) {
			const that = this
			this.message = newsChat.get_input_val(e)
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return newsChat.onShareAppMessage({});
	}
}
